'use client';
import React from 'react';
import { useTranslation } from 'react-i18next';
import { useTheme } from '@/context/ThemeContext';  // useTheme hook'unu import ettik
import styles from './login.module.scss';
import Link from 'next/link';

export default function LoginPage() {
  const { t } = useTranslation();
  const { theme } = useTheme();  // Tema bilgisini aldık

  return (
    <div className={`${styles.loginPage} ${theme === 'dark' ? styles.dark : styles.light}`}>
      <div className={styles.loginContainer}>
        <h1>{t('Login.title')}</h1>
        {/* Login formu buraya eklenebilir */}

        {/* Alt yönlendirme */}
        <p>
          {t('Login.noAccount')}{" "}
          <Link href="/register" className={`${styles.link} ${theme === 'dark' ? styles.darkLink : styles.lightLink}`}>
            {t('Login.registerLink')}
          </Link>
        </p>
      </div>
    </div>
  );
}
